    detail_t2019: 'To be or not to be? That is the question.',
    detail_c2019_1:
      'It is 1839, Lahore, and the Lion of Punjab, King Hamlet, has only recently been laid to rest. His brother Claudius is now crowned King and has married Queen Gertrude, his dead brothers wife. This ensures her only son, Prince Hamlet, is placed second in line to the throne. Prince Hamlet is heartbroken by the death of his beloved, and more revered, father. He is equally enraged and disgusted over the loss of his birthright, and the hasty marriage of his Uncle and mother, Queen Gertrude.',
    detail_c2019_2:
      'Hamlets loyal friend Horatio, and some of the guards of Lahore Fort, encounter a ghost in the form of the dead king, which leaves an uneasy sense of dread in their minds, and a question as to why the ghost has appeared. Horatio decides that Prince Hamlet should know of the appearance of the ghost and takes him to see it. Prince Hamlet becomes entranced by the sight of the ghost, making him forsake his friends, and follow the ghost when it beckons him, to learn why it has appeared',
    detail_c2019_3:
      'In the dungeon of the fort the ghost of the dead King reveals that he was poisoned by the evil Claudius, who now wears his crown and holds the hand of his beloved, and what he thought, faithful wife. This encounter sends Hamlet on a vengeful mission to reveal the guilt of his ambitious Uncle, who stole his rightful crown. ',
    detail_c2019_4:
      'The vengeful action of Hamlet sheds no mercy on innocent parties. He wrestles with his conscience, over what is right and wrong, and with life and death, in order to seek justice. Ophelia, Hamlets loyal sweetheart weeps for his supposed loss of mind and slowly slips into her own madness when he rejects her love. Hamlet also accidentally kills her father in a fit of rage. Ophelia, in a fragile state of mind becomes melancholic and tragically takes her own life by drowning.Ophelias older brother Laertes, returns from France, upon hearing of the death of his father and confronts King Claudius seeking justice and the truth about his fathers death. He understands how the situation has destroyed his sisters mind and in his grief, and thirst for vengeance, is deceived by King Claudius, who plans to use him to ensure the death of Hamlet.',
    detail_c2019_5:
      'A duel is arranged between Laertes and Hamlet. King Claudius poisons a cup of wine and Laertes has poisoned the blade of his sword for when he strikes Hamlet. Innocently, Queen Gertrude drinks from the cup, and in her final moments realizes what has happened and declares the wine is poisoned. Hamlet is distracted by her cries, and is wounded by Laertes poisoned sword. Hamlet and Laertes scuffle and blades are switched, leaving Laertes mortally wounded. As Laertes staggers and dies he reveals the plan of King Claudius to kill Hamlet, and ask for Hamlets forgiveness. Enraged, Hamlet stabs King Claudius with the poisoned sword thereby keeping the promise he made to his fathers ghost to avenge his death. As Hamlet lays drying in the arms of his loyal friend Horatio, he asks Horatio to tell his tale.',
    detail_c2019_6:
      'So ends the tragic story of the Dark Prince Hamlet, the last Maharaja of Lahore.',
